<template>
    <div>
        <v-card class="mx-auto ma-5" max-width="1000px" :flat="$vuetify.breakpoint.smAndDown">
            <v-row v-if="paramTag!==undefined">
                <v-col cols="2">
                    <v-text-field outlined class="pa-1" v-model="paramTag.id" disabled hide-details label="标签序号"/>
                </v-col>
                <v-col cols="10">
                    <v-text-field outlined class="pa-1" v-model="paramTag.name" clearable hide-details label="标签名称"/>
                </v-col>
            </v-row>
            <v-row v-else>
                <v-col>
                    <v-text-field outlined class="pa-1" v-model="tag.name" clearable hide-details label="标签名称"/>
                </v-col>
            </v-row>
        </v-card>
        <div class="d-flex justify-end align-center" style="position: fixed; bottom: 60px; right: 60px; z-index: 10">
            <v-slide-x-reverse-transition>
                <v-alert v-model="alertConfig.alertOpen" class="ma-5" :type="alertConfig.alertType"
                         elevation="5" dismissible>
                    {{alertConfig.alertMessage}}
                </v-alert>
            </v-slide-x-reverse-transition>
            <v-btn fab :loading="loading" color="green" dark @click="insertOrUpdate" fixed bottom right
                   style="bottom: 80px">
                {{paramTag===undefined?"新增":"更新"}}
            </v-btn>
        </div>
        <v-snackbar v-model="snackbar" color="success">
            {{paramTag===undefined?"标签新增成功":"标签更新成功"}}
        </v-snackbar>
    </div>
</template>

<script>

    export default {
        name: "NewTag",
        props: {},
        components: {},
        data() {
            return {
                loading: false,
                paramTag: undefined,
                tag: {
                    name: ""
                },
                alertConfig: {
                    alertOpen: false,
                    alertType: "info",
                    alertMessage: ""
                },
                snackbar: false
            }
        },
        mounted() {
        },
        beforeDestroy() {
        },
        methods: {
            insertOrUpdate() {
                if (this.paramTag !== undefined) {
                    this.update()
                } else {
                    this.insert()
                }
            },
            insert() {
                if (this.tag.name === "") {
                    this.alertConfig = {
                        alertOpen: true,
                        alertType: "error",
                        alertMessage: "标签名称为空"
                    }
                } else {
                    this.loading = true
                    let _this = this
                    this.axios.put(
                        this.API.URL.INSERT_TAG,
                        JSON.stringify(this.tag),
                        {
                            headers: {'Content-Type': 'application/json'},
                        })
                        .then(function (result) {
                            _this.loading = false
                            let data = result.data
                            if (data.success) {
                                console.log("data", data)
                                // _this.tag.id = data.data.id
                                _this.tag = {
                                    name: ""
                                }
                                _this.snackbar = true
                            } else {
                                _this.alertConfig = {
                                    alertOpen: true,
                                    alertType: "error",
                                    alertMessage: data.errorMsg
                                }
                            }
                        }, function (error) {
                            _this.loading = false
                            console.log(error)
                        })
                }
            },
            update() {
                if (this.paramTag.name === "") {
                    this.alertConfig = {
                        alertOpen: true,
                        alertType: "error",
                        alertMessage: "标签名称为空"
                    }
                } else {
                    this.loading = true
                    let _this = this
                    this.axios.post(
                        this.API.URL.UPDATE_TAG,
                        JSON.stringify(this.paramTag),
                        {
                            headers: {'Content-Type': 'application/json'},
                        })
                        .then(function (result) {
                                _this.loading = false
                                let data = result.data
                                if (data.success) {
                                    console.log("data", data)
                                    // _this.tag.id = data.data.id
                                    _this.paramTag = data.data
                                    _this.snackbar = true
                                } else {
                                    _this.alertConfig = {
                                        alertOpen: true,
                                        alertType: "error",
                                        alertMessage: data.errorMsg
                                    }
                                }
                            }, () => _this.loading = false
                        )
                }
            }
        },
        watch: {
            "$route": {
                handler() {
                    setTimeout(() => {
                        if (this.$route.params.tag !== undefined) {
                            console.log(this.$route)
                            this.paramTag = this.$route.params.tag
                        }
                    }, 300)
                },
                // 首次进入页面即执行
                immediate: true
            }
        }
    }
</script>

